.NavWrap {
  width: 64px;
  height: 60%;
  position: fixed;
  top: 120px;
  left: 3px;
  background-color: #343f4d;
}

.navUl {
  list-style: none;
  margin-left: -35px;
}

.NavWrap ul li {
  padding-top: 10px;
}

.NavWrap ul li a {
  text-decoration: none;
  font-size: 10px;
}

.NavWrap ul li a:visited{
  color: #fff;
}
.NavWrap ul li a:link{
  color: #fff;
}



